import { Constants } from "../Engines/constants";
import { Color3 } from "../Maths/math";
import { Scene } from "./scene";
export class SceneFog {
    constructor(scene) {
        // Fog
        this._fogEnabled = true;
        this._fogMode = Scene.FOGMODE_NONE;
        /**
        * Gets or sets the fog color to use
        * @see https://doc.babylonjs.com/babylon101/environment#fog
        * (Default is Color3(0.2, 0.2, 0.3))
        */
        this.fogColor = new Color3(0.2, 0.2, 0.3);
        /**
        * Gets or sets the fog density to use
        * @see https://doc.babylonjs.com/babylon101/environment#fog
        * (Default is 0.1)
        */
        this.fogDensity = 0.1;
        /**
        * Gets or sets the fog start distance to use
        * @see https://doc.babylonjs.com/babylon101/environment#fog
        * (Default is 0)
        */
        this.fogStart = 0;
        /**
        * Gets or sets the fog end distance to use
        * @see https://doc.babylonjs.com/babylon101/environment#fog
        * (Default is 1000)
        */
        this.fogEnd = 1000.0;
        this.scene = scene;
    }
    /**
    * Gets or sets a boolean indicating if fog is enabled on this scene
    * @see https://doc.babylonjs.com/babylon101/environment#fog
    * (Default is true)
    */
    set fogEnabled(value) {
        if (this._fogEnabled === value) {
            return;
        }
        this._fogEnabled = value;
        this.scene.markAllMaterialsAsDirty(Constants.MATERIAL_MiscDirtyFlag);
    }
    get fogEnabled() {
        return this._fogEnabled;
    }
    /**
    * Gets or sets the fog mode to use
    * @see https://doc.babylonjs.com/babylon101/environment#fog
    * | mode | value |
    * | --- | --- |
    * | FOGMODE_NONE | 0 |
    * | FOGMODE_EXP | 1 |
    * | FOGMODE_EXP2 | 2 |
    * | FOGMODE_LINEAR | 3 |
    */
    set fogMode(value) {
        if (this._fogMode === value) {
            return;
        }
        this._fogMode = value;
        this.scene.markAllMaterialsAsDirty(Constants.MATERIAL_MiscDirtyFlag);
    }
    get fogMode() {
        return this._fogMode;
    }
}
