        this.sceneClipPlane = new SceneClipPlane();
        this.sceneCatch = new SceneCatch(this);
        this.sceneInputManagerApp = new SceneInputManagerApp(this);
        this.sceneEventTrigger = new SceneEventTrigger(this);
